/* eslint-disable import/no-extraneous-dependencies */
/** @jsx jsx */
import { Value, Controller, History } from '@ali/4ever-cangjie';
import '@testing-library/jest-dom/extend-expect';

import {
  jsx, waitForTime,
} from '@ali/4ever-dev-test';
import { createBiPlugins } from '../..';


const plugins = createBiPlugins({}).array;

const defaultValue = (
  <value>
    <document>
      <block type="paragraph" key="test-color"><focus/>Hello Cangjie<cursor /></block>
    </document>
  </value>
);

const DEFAULT_GRADIENT_COLOR = '#F3AA00__#E43E29';

describe('Bi/color/setColor', () => {
  describe('queries/getAlignment', () => {
    test('设置渐变色', () => {
      const controller = Controller.create({
        value: defaultValue,
        plugins,
      });
      controller.dispatch('setColor', DEFAULT_GRADIENT_COLOR);

      const color = controller.query('getColor');
      expect(color).toBe(DEFAULT_GRADIENT_COLOR);
    });
  });
});
